import React, { useEffect, useState } from 'react';
import axios from 'axios';
import useStyles from '../Style/Style';
import { Grid, Paper, CircularProgress } from '@material-ui/core';

const Detail = ({ match }) => {
    const classes = useStyles();
    const [state, setState] = useState({ isLoading: true, data: [] });

    useEffect(() => {
        function fdata() {
            axios.get(`https://www.googleapis.com/blogger/v3/blogs/4606092665974021629/posts/${match.params.id}?key=AIzaSyDci4ioLXX-x0SsC88MOQk399SO0PMPR14`)
                .then((res) => {
                    setState((s) => ({ ...s, isLoading: false, data: res.data }));
                });
        }
        fdata();
    }, [match.params.id])

    // function getImage(){
    //         let c = document.getElementById('content');
    //         let sel = c.querySelector('img')
    //         if (sel === null) {
    //             return null;
    //         } else {
    //             return sel.src;
    //         }
    // }

    // <Helmet>
    //             <title>MMuflih - </title>
    //             <meta property="og:description" content="Helmet application" />
    //             <meta property="og:url" content="Helmet application" />
    //             <meta property="og:title" content="Helmet application" />
    //             <meta property="og:image" content="Helmet application" />
    //             <meta name="twitter:card" content="Helmet application" />
    //             <meta name="twitter:image" content="Helmet application" />
    //         </Helmet>

    return (
            <Grid container spacing={2}>
                <Grid item xs={12} md={12} sm={12}>
                    <Paper className={classes.container}>
                        {state.isLoading === true ? (
                            <div style={{ display: 'flex', minHeight: '80vh', justifyContent: 'center', alignItems: 'center', height: 'inherit', padding: '10px', width:'100%' }}>
                                <CircularProgress />
                            </div>
                        ) : (
                                <div>
                                    <h1>{state.data.title}</h1>
                                    <p>{new Date(state.data.updated).toLocaleDateString()} {new Date(state.data.updated).toLocaleTimeString()}</p>
                                    <div id="content" dangerouslySetInnerHTML={{ __html: state.data.content }}></div>
                                </div>
                            )}
                    </Paper>
                </Grid>
            </Grid>
    )
}

export default Detail;